.navigation {
    background-color: rgba(0, 0, 0, .3);
    height: 208px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;

    padding: 0 91px;
}

.inner-container {
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.nav-link {
    font-size: 36px;
    text-align: center;
    color: #fff;
    text-decoration: none;

    padding: 0 50px;

    border-right: 1px solid #fff;
    -ms-flex-negative: 1;
        flex-shrink: 1;
}

.nav-link:last-child {
    border-right: none;
    padding-right: 0;
}

.nav-link:hover,
.nav-link:active {
    color: #e86706;
}


