.product-card{
    margin-bottom: 76px;
}

.product-card-title {
    font-size: 24px;
    color: #3d49b8;
    margin: 0;
    max-width: 820px;
}

.product-card-title-pros-cons {
    margin: 14px 0;
}

.product-card-subtitle {
    font-size: 18px;
    margin: 0;
    max-width: 820px;
}

.product-card-pic {
    width: 820px;
    min-height: 295px;
    margin-right: 15px;
    margin-top: 26px;
}

.product-card-contents {
    display: -ms-flexbox;
    display: flex;
}

.pros-cons {
    line-height: 1.4;
    padding: 0 0 0 15px;
}


