.form-container {
    background: -webkit-gradient(
        linear,
        left top, left bottom,
        from(rgba(0, 0, 0, 0.7)),
        to(rgba(0, 0, 0, 0.7))
      ), url('http://esconrussia.ru/wp-content/themes/escon/images/about_bg2.jpg');
    background: -webkit-linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      ), url('http://esconrussia.ru/wp-content/themes/escon/images/about_bg2.jpg');
    background: -o-linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      ), url('http://esconrussia.ru/wp-content/themes/escon/images/about_bg2.jpg');
    background: linear-gradient(
        rgba(0, 0, 0, 0.7),
        rgba(0, 0, 0, 0.7)
      ), url('http://esconrussia.ru/wp-content/themes/escon/images/about_bg2.jpg');
    height: 448px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0 220px;
}

.form-container-title {
    font-size: 55px;
    text-align: center;
    color: #ee8e46;
}

.form-container-subtitle {
    font-size: 45px;
    text-align: center;
    color: #ffffff;
}

.form-container-subtitle--orange {
    color: #ee8e46;
}

.form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;

}

.form-input {
    width: 33%;
    height: 67px;
    border-radius: 10px;
    font-size: 30px;
    padding-left: 20px;
}

.form-btn {
    width: 27%;
    height: 67px;
    border-radius: 10px;
    background-color: #3d49b8;
    color: #fff;
    font-size: 30px;
    border: none;
}



