.App {
  min-width: 1366px;
}

.main-container {
  background-color: rgba(0, 0, 0, .1);
}

.text-section {
  max-width: 1162px;
  margin: auto;
}

.description-title {
  text-align: center;
  font-size: 30px;
  color: #3d49b8;
  margin: 46px 0 31px;
}

.description-title--orange {
  color: #e86706;
}

.description-text {
  font-size: 18px;
  text-indent: 1rem;
}

.pics {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.construction-pic {
  -ms-flex-positive: 1;
      flex-grow: 1;
  /* width: 868px; */
  /* height: 489px; */
}

.pic-container {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
}

.small-pic {
  width: 40%;
  margin-right: 20px;
  margin-bottom: 12px;
  /* height: 150px; */
}

.small-pic:nth-last-child(1),
.small-pic:nth-last-child(2) {
  margin-bottom: 0;
}

.list-title {
  font-size: 30px;
  font-weight: bold;
  color: #e86706;
}

.bottom-pics {
  text-align: center;
}

.bottom-pic {
  margin-right: 50px;
}


